
.container{ 
    margin-left: auto;
    margin-right: auto;
    padding: 0 20px;
    width: 100%;
    @media screen and (min-width:480px) {
        width: 480px; 
    }
    @media screen and (min-width:768px) {
        padding: 0 34px;
        width: 768px;           
    }
    @media screen and (min-width:1280px) {
        padding: 0 116px;
        width: 1280px;           
    }
} 